<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus consultas
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Consultas pendientes</v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <div class="mb-2" style="color: #084068">{{ consultation.service_name }}</div>

          <div class="call-to-action">
            <v-row style="background-color: #184C72; color: white">
              <v-col class="text-left">Estado: {{ consultation.status.name }}</v-col>
              <v-col class="text-right">{{ consultation.created_at | dateTime }}</v-col>
            </v-row>

            <v-row>
              <v-col class="text-center">
                <div>Costo del documento : S/. {{ consultation.price }}</div>
                <div>Código de solicitud: {{ consultation.id | padleft }}</div>
                <div>Abogado responsable: {{ consultation.lawyer.name }}</div>
                <div v-if="canSeeDateTime">Fecha y Hora: {{ consultation.appointment | dateTime }}</div>
                <div v-if="canSeeMeetingUrl">Meeting URL: {{ consultation.meeting }}</div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <div class="call-to-action-white" style="margin: 1rem; position: relative">
            <div class="text">
              Para completarla tu solicitud envianos el comprobante del depósito. (Imagen JPG,
              PNG O PDF. Máximo 5 Mb.)
            </div>
            <img :src="require('../../assets/icons/lawyer-07.svg')" style="position: absolute; right: 0; bottom: 0">
          </div>
        </v-col>
        <v-col v-if="consultation.hasVoucher" cols="12">
          <v-btn color="secondary" block :href="consultation.voucherUrl" target="_blank">Comprobante actual</v-btn>
        </v-col>
        <v-col cols="12">
          <v-file-input placeholder="seleccionar comprobante..." v-model="file" @change="onAttach"></v-file-input>
        </v-col>
        <v-col cols="12">
          <v-btn @click="onSubmit" :loading="isLoading" color="primary" block>Enviar</v-btn>
        </v-col>
        <v-col cols="9"></v-col>
        <v-col cols="3" class="text-center" @click="goToIndex">
          <v-img
              :src="require('../../assets/icons/home-12.svg')"
              contain
              height="48"
          />
          Regresar
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"

export default {
  name: 'RequestPayment',
  inject: ['notyf'],
  data: () => ({
    isLoading: false,
    consultation: {
      status: '',
      lawyer: '',
    },
    file: ''
  }),
  computed: {
    ...mapGetters(['user']),
    canSeeDateTime() {
      return (this.consultation.service_id === 2 || this.consultation.service_id === 3) && this.consultation.appointment
    },
    canSeeMeetingUrl() {
      return this.consultation.service_id === 2 && this.consultation.meeting
    }
  },
  mounted() {
    this.loadConsultation()
  },
  methods: {
    loadConsultation() {
      let consultationId = this.$route.params.id
      let url = 'consultations/' + consultationId
      axios.get(url).then((response) => {
        this.consultation = response.data
      })
    },
    onAttach(file) {
      this.file = file
    },
    onSubmit() {
      this.isLoading = true
      let consultationId = this.$route.params.id

      let formData = new FormData();
      formData.append('_method', 'PUT');
      formData.append('voucher', this.file);

      axios.post('consultations/' + consultationId, formData, {headers: {'Content-Type': 'multipart/form-data'}}
      ).then((response) => {
        this.consultation = response.data
        this.notyf.success('Comprobante cargado correctamente.');

        this.$router.push({name: 'RequestPendingConfirm', params: {id: response.data.id}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      });
    },
    goToIndex() {
      this.$router.push({name: 'RequestIndex'})
    }
  }
}
</script>
